<template lang="pug">
.blocks
  .block(
    v-for="fieldConfig,fieldName in fields"
    v-show="isShown(fieldConfig)"
    :class="[colClass(fieldConfig,fieldName)]")
    
    //Before Slots
    slot(:name="`field--before--${fieldName}`" v-bind="slotScopes(fieldName)")
    slot(v-if="index!=null" :name="`field--before--${fieldName}--${index}`" v-bind="slotScopes(fieldName)")

    //Field
    component(
      :is="whichComponent(fieldConfig)"
      :index="index"
      :key="fieldName"
      :name="fieldName"
      :fields="fieldConfig.fields" 
      :config="fieldConfig"
      :value="groupValue && groupValue[fieldName]"
      :group-value="groupValue"
      :group-meta-value="groupMetaValue"
      @updateRepeaterConfig="$emit('updateRepeaterConfig',$event)"
      @input="input(arguments,fieldName)")

      //Passdown Slots
      template(v-for="slot in Object.keys($scopedSlots)" v-slot:[slot]="scope")
        slot(:name="slot" v-bind="scope")

    //After Slots
    slot(:name="`field--after--${fieldName}`" v-bind="slotScopes(fieldName)")
    slot(v-if="index!=null" :name="`field--after--${fieldName}--${index}`" v-bind="slotScopes(fieldName)")
      
</template>

<script>
export default {
  name: "fields",
  inject: ["CONFIG"],
  mixins: [
    //Because this also behaves as interface when grouped.
    require("@/plugin/mixins/interface").default,
    require("@/plugin/helper").default
  ],
  props: {
    config: {},
    fields: {},
    index: {
      default: null,
      type: Number
    },
    name: {
      default: null,
      type: String
    }
    // groupValue: {},
    // groupMetaValue: {}
  },
  data() {
    return {
      // Need to clone values to avoid directly mutating props.
      groupValue: { ...this.value },
      groupMetaValue: {}
    };
  },

  methods: {
    input(args, name) {
      this.$set(this.groupValue, name, args[0]);

      this.groupMetaValue = {
        ...this.groupMetaValue,
        //Last element of changes array contains all the metaValues
        [name]: args[1][args[1].length - 1].metaValue
      };

      let changed = {
        field: this.name,
        action: "group-input",
        value: this.groupValue,
        metaValue: this.groupMetaValue,
        index: this.index
      };
      let changeMerged = [...args[1], changed];
      this.$emit("input", this.groupValue, changeMerged);
    },

    slotScopes(name) {
      return {
        value: this.value && this.value[name],
        metaValue: this.metaValue && this.metaValue[name],
        groupValue: this.groupValue,
        groupMetaValue: this.groupMetaValue,
        config: this.fields[name],
        index: this.index
      };
    },

    /**
     * Sets the grid size of ss-cols
     * If not defined, gets it from default configs
     */
    colClass(field, name) {
      let classes = [`block--${name}`];
      let col =
        (field.design && field.design.col) ||
        (this.CONFIG.defaults.design && this.CONFIG.defaults.design.col) ||
        12;
      classes.push(`is-${col}`);
      return classes;
    },

    isShown(config) {
      if (config.hide === undefined || config.hide === null) {
        return true;
      } else {
        return !config.hide;
      }
    },

    whichComponent(config) {
      if (config.repeater) {
        return "repeater";
      } else {
        return "field";
      }
    }
  }
};
</script>